.block-img {
  width: 120px;
  height: 150px;
  border: 1px solid #000;
  border-radius: 10px;
  padding-top: 5px;
  background-color: coral;
  box-shadow: 5px 5px 15px 5px #000000;
}
.block-img img {
  width: 100px;
  height: 100px;
}
.block-date {
  border: 1px solid #000;
  background-color: coral;
  width: 120px;
  margin-top: 10px;
  box-shadow: 5px 5px 15px 5px #000000;
}
